import React, { useState } from 'react';
import PropTypes from 'prop-types';

export default function ImageTextOptionElement(props) {
  const { image, label } = props;
  const holderStyle = {
    width: 100,
    height: 100
  };

  const childImageStyle = {
    width: '100%',
    height: '100%',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundImage: `url(${image})`
  };

  const [allStyles, setAllStyles] = useState(null);

  setAllStyles(childImageStyle);

  const zoomIn = e => {
    const newObj = { ...childImageStyle, transform: 'scale(2.2' };
    setAllStyles(newObj);
  };

  return (
    <div style={holderStyle}>
      <div style={allStyles} onMouseEnter={zoomIn}>
        <p>{label}</p>
      </div>
    </div>
  );
}

ImageTextOptionElement.propTypes = {
  image: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired
};
